ul.tags {
  padding: 0;
  margin: 0;
  list-style: none;
}

ul.tags > li {
  display: inline-block;
  padding: 2px 6px 3px;
  font-size: 12px;
  color: #878787;
  background-color: #ebebeb;
  border-radius: 5px;
  opacity: .8;
}

ul.tags > li:not(:last-child) {
  margin-right: 10px;
}
